
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  display: block;
}
#submit{
  margin:auto;
  padding-top: 10px;
}
button {
  display: inline-block;
  position: relative;
  padding: 10px;
  top: 0;
  font-size: 20px;
  font-family: "Open Sans", Helvetica;
  border-radius: 4px;
  border-bottom: 1px solid rgba( 28, 227, 125, 0.5 );
  background: rgba( 22, 230, 137, 1 );
  color: #fff;
  -webkit-box-shadow: 0px 0px 0px rgba( 15, 165, 60, 0.1 );
          box-shadow: 0px 0px 0px rgba( 15, 165, 60, 0.1 );
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  -webkit-transition: all 0.2s ease;
          transition: all 0.2s ease;
}
button:hover {
  top: -10px;
  -webkit-box-shadow: 0px 10px 10px rgba( 15, 165, 60, 0.2 );
          box-shadow: 0px 10px 10px rgba( 15, 165, 60, 0.2 );
  -webkit-transform: rotateX(20deg);
          transform: rotateX(20deg);
}
button:active {
  top: 0px;
  -webkit-box-shadow: 0px 0px 0px rgba( 15, 165, 60, 0.0 );
          box-shadow: 0px 0px 0px rgba( 15, 165, 60, 0.0 );
  background: rgba( 20, 224, 133, 1 );
}

img {
  margin: 0 auto;
}

p[data-v-df9a9462] {
  font-size: 2em;
  text-align: center;
}

.crypto{
  margin:auto;
  padding-top: auto;
}

#AddCoinBtn{
  border-bottom: 1px solid rgba(227, 207, 28, 0.5);
  background: rgb(192, 230, 22);
}
#AddCoinBtn:active {
  background: rgb(136, 224, 20);
}
.BasicInputText{
  position: relative;
}
